.login_btn{
    padding: 30px 50px;
    border-radius: 70px;
    
    background-color: rgb(47, 202, 202);
    color: rgb(255, 255, 255);
    font-weight: 500;
    font-size: 30px;
  
    &:hover{
      border-style: solid;
      background-color:transparent;
      border-color: rgb(47, 202, 150);
      color: black;
      text-decoration: none;
      transition: 1s;
    }
  }

